import request from '@/utils/request'
import _ from 'lodash'

const init_archive = {
  id:0,
  type_id:0,
  title:'',
  flag:[],
  litpic:'',
  content:'',
  uid:0,
  keywords:'',
  description:'',
  click:0,
  create_time:'',
  edit_time:'',
  status:1
}
const state = {
  archiveApi: '/api/archive',
  archive:init_archive,
}

const mutations = {
  SET_ARCHIVE: (state, archive) =>{
    if (archive === undefined){
      //  数据深度拷贝 保证数据初始化成功
      state.archive = _.cloneDeep(init_archive)
    } else{
      state.archive = archive
    }
  }
}

const actions = {

  // edit page => init data
  initial({state, dispatch, commit}, id){
    if (id) {
      // edit
      dispatch('fetchData',id)
    } else{
      // post
      commit('SET_ARCHIVE')
    }
    dispatch('arctype/fetchTypeOptions', false, {root:true})
    dispatch('user/fetchUserOptions', null, {root:true})
  },

  // edit page => get archive by id
  fetchData({commit, state}, id){
    request.get(state.archiveApi+'/'+ id).then( resp =>{
      if (resp) {
        commit('SET_ARCHIVE', resp.data)
      }
    })
  },

  // create/update  archive => put post
  editArchive({state}){
    const data = state.archive
    let formData = new FormData();
    Object.keys(data).forEach((key) =>{
      formData.append(key,data[key])
    })
    let config = {
      headers:{
        'Content-Type':'multipart/form-data'
      }
    }
    const id = state.archive.id
    if (id){
      // put
      return request.put(state.archiveApi, formData)

    }else {
      // post
      return request.post(state.archiveApi, formData)
    }
  }
}


export default {
  namespaced:true,
  state,
  actions,
  mutations
}



