
import request from '@/utils/request'

const state = {
  user_total: 0,
  article_total: 0,
  user_near_num: 0,
  comment_near_num: 0,
  login_log_list: [],
  server_current_time:''
}
const  mutations = {
  SET_SERBER_CURRENT_TIME: (state, time) =>{
    state.server_current_time = time
  },
  SET_USER_TOTAL: (state, total) =>{
    state.user_total = total
  },
  SET_ARTICLE_TOTAL: (state, total) =>{
    state.article_total = total
  },
  SET_USER_NEAR_NUM: (state, number) =>{
    state.user_near_num = number
  },
  SET_COMMENT_NEAR_NUM: (state, number) =>{
    state.comment_near_num = number
  },
  SET_LOGIN_LOG_LIST: (state, list) => {
    state.login_log_list = list
  }
}

const actions = {
  fetchData({commit}){
    request.get('/api/admin').then( resp =>{
      if (resp){
        const data = resp.data
        commit('SET_SERBER_CURRENT_TIME', data['current_time'])
        commit('SET_USER_TOTAL', data['user_total'])
        commit('SET_ARTICLE_TOTAL', data['article_total'])
        commit('SET_USER_NEAR_NUM', data['user_near_num'])
        commit('SET_COMMENT_NEAR_NUM', data['comment_near_num'])
        commit('SET_LOGIN_LOG_LIST', data['login_log_list'])
      }
    })
  }
}

export default {
  namespaced:true,
  state,
  mutations,
  actions
}



