import _ from 'lodash'
import request from '@/utils/request'

const init_config = {
  id: 0,
  key: '',
  value: '',
  type: '',
  description: ''
}
const type_options = [
  { value:'sys', text: '后台配置' },
  { value: 'web', text: '前台配置'}
]

const state = {
  config: init_config,
  type_options,
  configAPI: '/api/config'
}

const mutations = {
  SET_TYPE_OPTIONS: (state, options) =>{
    state.type_options = options
  },
  SET_CONFIG: (state, config) =>{
    if (config === undefined){
      state.config = _.cloneDeep(init_config)
    } else{
      state.config = config
    }
  }
}

const actions = {
  fetchConfig({commit, state}, uuid){
    if (uuid){
      request.get(state.configAPI + '/' + uuid).then( resp =>{
        if (resp){
          commit('SET_CONFIG', resp.data)
        }
      })
    } else{
      commit('SET_CONFIG')
    }
  },

  editConfig({commit, state}){
    const uuid = state.config.id
    if (uuid){
      // 更新
      return request.put(state.configAPI+ '/'+uuid, state.config)
    } else{
      // 插入
      return request.post(state.configAPI, state.config)
    }
  }
}


export default {
  namespaced:true,
  state,
  mutations,
  actions
}



