
import axios from 'axios'
import router from '../../router/index'
const request = axios.create({
  timeout:5000,
  withCredentials:true,
})

// 相应拦截器
request.interceptors.response.use(resp =>{
  const data = resp.data
  if (data.code === 0){
    return data
  }
}, error => {
  const error_resp = error.response
  const status_code = error_resp.status
  switch (status_code){
    case 404:
      router.push({path:'/404'})
  }
  return Promise.reject(error)
})


const state = {
  web_config: {
    web_author:"",
    web_title: '',
    git_fork_url: '',
    git_fork_img:'',
    git_star_url: '',
    git_star_img: '',
    web_footer: '',
    web_views: 0,
    comments:0,
  },
  web_avatar: require('@static/uploads/avatar/header.jpg'),
  type_id: 0,
  // 左侧导航栏 list
  sidebar:[],
  // 文章详细数据
  archive:[],
  // 横向导航 list
  navbar:[],
  // 上一篇文章数据
  pre_acticle:{},
  // 下一篇文章数据
  next_acticle:{},
  // 回复类型 0：留言板 1：文章
  reply_type: 0,
  comment_list: [],
  comment_user_total:0,
  comment_total: 0
}

const mutations = {
  SET_COMMENT_TOTAL: (state, total) =>{
    state.comment_total = total
  },
  SET_WEB_CONFIG: (state, config) =>{
    state.web_config = config
  },

  SET_COMMENT_USER_TOTAL: (state, total) =>{
    state.comment_user_total = total
  },

  SET_COMMENT_LIST: (state, list) =>{
    state.comment_list = list
  },
  SET_REPLY_TYPE: (state, reply_type) =>{
    state.reply_type = reply_type
  },

  SET_PRE_ACTICLE: (state, data) =>{
    state.pre_acticle = data
  },
  SET_NEXT_ACTICLE: (state, data) =>{
    state.next_acticle = data
  },
  SET_NAVBAR: (state, navs) =>{
    state.navbar = navs
  },
  SET_TYPE_ID: (state, id) =>{
    localStorage.setItem('type_id', id)
    state.type_id = id
  },
  SET_SIDEBAR: (state, data) =>{
    state.sidebar = data
  },
  SET_ARCHIVE: (state, archive) =>{
    state.archive = archive
  }
}

const actions = {
  fetchHot(context){
    return request.get('/api/front/hot')
  },

  // fetch web_config
  async fetchWebConfig({commit}){
    await request.get('/api/config/web_config').then(resp =>{
      if (resp){
        commit('SET_WEB_CONFIG', resp.data)
      }
    })
  },
  // fetch arctype sidebar
  fetchSidebar({commit}){
    request.get('/api/arctype/front').then( resp =>{
      if (resp){
        commit('SET_SIDEBAR', resp.data)
      }
    })
  },
  fetchArctype({commit, dispatch}, uuid){

    request.get('/api/arctype/front/type_id/'+uuid).then( resp =>{
      if (resp){
        commit('arctype/SET_ARCTYPE', resp.data, {root: true})
      }
    })
    // dispatch('fetchNavs')
    // dispatch('fetchComments')
  },

  // fetch arctype navbar by type_id
  fetchNavs({commit, state}){
    let type_id = state.type_id || localStorage.getItem('type_id')
    if (!type_id){ type_id = 0}
    request.get('/api/arctype/front/id/'+ type_id).then( resp =>{
      if (resp){
        commit('SET_NAVBAR', resp.data)
      }
    })
  },
  // fetch article data by article.id
  async fetchArticle({commit, dispatch}, uuid){
    await request.get('/api/archive/front/id/'+ uuid).then( resp =>{
      if (resp){
        commit('archive/SET_ARCHIVE', resp.data['current_data'], {root:true})
        commit('SET_PRE_ACTICLE', resp.data['pre_data'])
        commit('SET_NEXT_ACTICLE', resp.data['next_data'])
        commit('SET_REPLY_TYPE', resp.data['current_data']['id'])
      }
    })
    // dispatch('fetchNavs')
    // dispatch('fetchComments')
  },

  // front index page get archive list by type_id
  async fetchArchive({state, commit}){
    // init archive list data => []
    commit('SET_ARCHIVE', [])
    const type_id = state.type_id || localStorage.getItem('type_id') || 0
    await request.get('/api/archive/front/type_id/'+type_id).then( resp =>{
      if (resp){
        commit('SET_ARCHIVE', resp.data)
      }
    })
  },

  refreshLike({commit}, uuid){
    return request.put('/api/archive/front/like/id/'+uuid)
  },

  postComment({state}, postData){
    postData['reply_type'] = state.reply_type
    return request.post('/api/comment/front', postData)
  },

  fetchComments({commit}, type_id){
    request.get('/api/comment/front/treeList/'+type_id).then( resp =>{
      if (resp){
        commit('SET_COMMENT_LIST', resp.data['treeList'])
        commit('SET_COMMENT_USER_TOTAL', resp.data['user_total'])
        commit('SET_COMMENT_TOTAL', resp.data['comment_total'])
      }
    })
  }
}

export default {
  namespaced:true,
  state,
  mutations,
  actions
}



