import axios from 'axios'
import {toast, getToken} from './index'
import router from '../router/index'

// 定义后台axios
const instance = axios.create({
  timeout:5000,
  withCredentials:true,
})
// 请求拦截器
instance.interceptors.request.use(config =>{
    // ..
    const token = getToken('token')
    if (token) {
      config.headers.Authorization = token
    }
    return config

  }, error => {
    return Promise.reject(error)
  })

// 相应拦截器
instance.interceptors.response.use(resp =>{
    const data = resp.data
    // 后台API验证成功状态码
    if (data.code === 0){
      return data
    } else{
      toast.warn({
        title: 'API 请求失败',
        message: data.error
      })
    }

  }, error => {
  console.log(error)
    const error_resp = error.response
    const status_code = error_resp.status
    const status_text = error_resp.statusText

    switch (status_code){
      case 500:
        toast.error({
          title:'服务器内部错误!',
          message:status_text
        }); break;

      case 400:
        let message = error_resp.data.match(/<p>([\s\S]*?)<\/p>/)[1]
        toast.error({
          title:'400 Bad Request',
          message:message
        }); break;
      case 401:
        message = error_resp.data.match(/<p>([\s\S]*?)<\/p>/)[1]
        toast.error({
          title:'401 Unauthorized',
          message:message
        });
        localStorage.removeItem('token')
        router.push({
          path:'/login'
        })
        break;
      case 403:
        message = error_resp.data.match(/<p>([\s\S]*?)<\/p>/)[1]
        toast.error({
          title:'403 Forbidden',
          message:message
        });break;
    }

    return Promise.reject(error)
  })

export default instance



