
import request from '@/utils/request'
import {encrypt, setToken} from "@/utils/index"
import _ from 'lodash'

const init_user = {
  id: 0,
  username: '',
  password: '',
  repass:'',
  image_code:'',
  token:'',
  nickname: '',
  email: '',
  mobile: '',
  sex: 0,
  logins: 0,
  create_time: '',
  edit_time: '',
  register_ip: '',
  last_time: '',
  status: 1,
  avatar: '',
  qq: '',
  birthday: '',
  info: '',
}
const init_group = {
  id: 0,
  name: '',
  module: '',
  level: 0,
  description: '',
  status:1,
  rule_array:[]
}
const init_rule = {
  pid: undefined,
  id: 0,
  name: '',
  route: '',
  method:'',
  type: 3,
  sorts: 0,
  status: 1
}

const state = {
  user: init_user,
  userAPI:'/api/user',
  groupAPI:'/api/user/group',
  ruleAPI:'/api/user/group/rule',
  user_options:[],
  auth_group: init_group,
  auth_rule: init_rule,
  rule_tree:[],
  rule_options: [],
  rule_checkbox: []
}

const mutations = {
  SET_RULE_OPTIONS: (state, options) =>{

    if (options.length > 0){
      state.auth_rule.pid = options[0].value
    }
    state.rule_options = options
  },
  SET_RULE_CHECKBOX: (state, array) =>{
    if (array === undefined){
      state.rule_checkbox = []
    } else {
      state.rule_checkbox = array
    }
  },

  SET_RULE_TREE: (state, rule_tree) =>{
    state.rule_tree = rule_tree
  },

  SET_AUTH_RULE: (state, auth_rule) =>{
    if (auth_rule === undefined){
      state.auth_rule = _.cloneDeep(init_rule)
    } else {
      state.auth_rule = auth_rule
    }
  },
  SET_USER_OPTIONS: (state, options) =>{
    state.user_options = options
  },
  SET_TOKEN: (state, token) =>{
    setToken('token', token)
    // localStorage.setItem('token', token)
    state.token = token
  },
  SET_AUTH_GROUP: (state, group) =>{
    if (group === undefined){
      state.auth_group = _.cloneDeep(init_group)
    } else {
      state.auth_group = group
    }
  },
  SET_USER: (state, user) =>{
    if (user === undefined){
      state.user = _.cloneDeep(init_user)
    } else {
      state.user = user
    }
  }
}


// 图片验证码
function validation_image_code(image_code){
  return request.post('/api/image_code/validation', { image_code })
}

// 用户登陆
function login(username, password, image_code) {
  // 图片验证码验证
  // 验证码验证成功 => 用户名密码加密验证
  const image_code_req = validation_image_code(image_code)
  return new Promise(resolve => {
    image_code_req.then(resp =>{
      // 验证成功 ..
      if (resp){
        username = encrypt(username)
        password = encrypt(password)
        request.post('/api/login', {username, password}).then( resp =>{
          // 用户验证返回
          resolve(resp)
        })
      }else{
        // 验证码失败的返回
        resolve(resp)
      }
    })
  })
}


const actions = {
  logout(context){
    return request.put('/api/logout')
  },
  fetchGroupList(context){
    return request.get('/api/user/auth')
  },
  editAuth(context, data){
    return request.post('/api/user/auth', data)
  },

  fetchUserAuth({commit}, uuid){
    return request.get('/api/user/auth/'+uuid)
  },

  editGroup({state}){
    const uuid = state.auth_group.id
    if (uuid){
      // put
      return request.put(state.groupAPI+'/'+uuid, state.auth_group)
    } else{
      // post
      return request.post(state.groupAPI, state.auth_group)
    }
  },

  editRule({state}){
    const uuid = state.auth_rule.id
    if (state.auth_rule.pid === undefined || state.auth_rule.pid.length === 0){
      state.auth_rule.pid = 0
    }
    if (uuid){
      // put
      return request.put(state.ruleAPI+'/'+uuid, state.auth_rule)
    } else{
      // post
      return request.post(state.ruleAPI, state.auth_rule)
    }
  },

  async fetchRuleTree({commit}){
    await request.get('/api/user/rules').then( resp =>{
      if (resp){
        commit('SET_RULE_TREE', resp.data)
      }
    })
  },


  async fetchGroup({commit, state}, uuid){
     await request.get(state.groupAPI+'/'+uuid).then( resp =>{
       if (resp) {
         commit('SET_AUTH_GROUP', resp.data['group_data'])
         commit('SET_RULE_CHECKBOX', resp.data['rule_data'])
       }
     })
  },

  async fetchRuleOptions({commit}){
    await request.get('/api/user/group/rule/options').then( resp =>{
      if (resp){
        commit('SET_RULE_OPTIONS', resp.data)
      }
    })
  },

  // group edit page => init data
  async groupInitial({state, dispatch, commit}, uuid){
    if (uuid) {
      // edit
      await dispatch('fetchGroup',uuid)
    } else{
      // post
      commit('SET_AUTH_GROUP')
    }
    await dispatch('fetchRuleTree')
  },


  async fetchRule({commit, state}, uuid){
    await request.get(state.ruleAPI+'/'+uuid).then( resp =>{
      if (resp) {
        commit('SET_AUTH_RULE', resp.data)
      }
    })
  },

  async ruleInitial({state, dispatch, commit}, uuid){
    if (uuid) {
      // edit
      await dispatch('fetchRule',uuid)
    } else{
      // post
      commit('SET_AUTH_RULE')
    }
    await dispatch('fetchRuleOptions')
  },

  // get user data of select options
  fetchUserOptions({commit}){
    request.get('/api/user/option').then( resp =>{
      if (resp){
        commit('SET_USER_OPTIONS', resp.data)
      }
    })
  },
  // check login [username, password, image_code]
  checkLogin({commit}, data){
    return login(data.username, data.password, data.image_code)
      .then(resp =>{
        if (resp){
          if (resp.data.token){
            const resp_data = resp.data
            commit('SET_TOKEN', resp_data.token)
          }
          return resp
        }
      })
  },
  editUser({state}){
    const data = state.user
    let formData = new FormData();
    Object.keys(data).forEach((key) =>{
      formData.append(key,data[key])
    })
    let config = {
      headers:{
        'Content-Type':'multipart/form-data'
      }
    }
    const id = state.user.id
    if (id){
      // put
      return request.put(state.userAPI+'/'+id, formData)

    } else{
      // post
      return request.post(state.userAPI, formData)
    }
  },
  fetchUser({commit}, id){
    if (id === undefined){
      commit('SET_USER')
      return
    }
    request.get(state.userAPI+'/'+ id).then( resp =>{
      if (resp) {
        commit('SET_USER', resp.data)
      }
    })
  }
}

export default {
  namespaced:true,
  state,
  mutations,
  actions
}



