import CXltToaStr from 'cxlt-vue2-toastr'
import 'cxlt-vue2-toastr/dist/css/cxlt-vue2-toastr.css'
import Vue from 'vue'
import '@static/global/jquery/jsencrypt.min'

export function encrypt(data) {
  if (!data) { return data }
  const public_key = '-----BEGIN PUBLIC KEY-----\n' +
    'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQC2tWlrxg+3WmTinSHNJnL5PTBL\n' +
    'KkI3LDn+l/pfDauKRcMStBS75Z+RsSus9R7dDTr/JSwJ8kFOrRiyLS2oTUIMnxlr\n' +
    'PlFm1l6FO9MiP7Txt0NTGuK212CVpt/7zcTUuOb4YmQcrzJSiA9ry0UfU7Lq3GrI\n' +
    '6nQxgu+LCbXvTx2KoQIDAQAB\n' +
    '-----END PUBLIC KEY-----';
  const rsa = new JSEncrypt();
  rsa.setPublicKey(public_key);
  return rsa.encrypt(data);
}

Vue.use(CXltToaStr, {
  position: 'top right',
  timeOut:3000,
  showMethod:'shake',
  hideMethod:'bounceOutRight'
})

// toast 提示框
export const toast = Vue.prototype.$toast

export function setToken(key, token, ex){
  const TOKEN_EXPIRE = 60 * 30
  try{
    if (!localStorage) { return false }
    if (!ex || isNaN(ex)) {
      ex = TOKEN_EXPIRE
    }
    const timestamp = (new Date() - 1) + ex * 1000;
    const cacheVal = { value: token, exp: timestamp }
    localStorage.setItem(key, JSON.stringify(cacheVal))
  }catch(e){ console.error(e) }
}

export function getToken(key){
  try{
    if (!localStorage) { return false }
    let ret = localStorage.getItem(key)
    ret = JSON.parse(ret)
    const now = new Date() - 1
    if (!ret) { return null }
    if (now >= ret.exp) {
      localStorage.removeItem(key)
      return null
    }
    return ret.value
  }catch(e){ console.error(e) }
}




