import request from '@/utils/request'
import _ from 'lodash'

const init_model = {
  id:0,
  name: '',
  operation: '',
  sorts: 50,
  status: 1,
}
const init_arctype = {
  id:0,
  pid: 0,
  typename:'',
  icon: '<i class="fa fa-circle-o"></i>',
  sorts:50,
  litpic:'',
  description:'',
  status:1,
  model_id:0,
  dirs:'',
  keywords:'',
  create_time:'',
  edit_time:''
}

const type_options = [
  { value: 0, text:'顶级分类'}
]

const state = {
  modelApi: '/api/arctype/model',
  arctypeApi: '/api/arctype',
  model:init_model,
  arctype:init_arctype,
  model_options: [],
  type_options
}

const mutations = {
  SET_Model_OPTIONS: (state, options) =>{
    if (options.length > 0 && state.arctype.id === 0){
      state.arctype.model_id = options[0].value
    }
    state.model_options = options
  },

  SET_TYPE_OPTIONS: (state, data) =>{
    const options = data['options']
    // 判断是否为编辑模式
    if (state.arctype.id ){
      options.forEach( vol =>{
        if (vol.value === state.arctype.id){
          vol['disabled'] = true
        }
      })
    }
    state.type_options = options

    if (data['un']){
      state.type_options.unshift(...type_options)
    }
  },

  SET_MODEL: (state, model) =>{
    if (model === undefined){
      //  数据深度拷贝 保证数据初始化成功
      state.model = _.cloneDeep(init_model)
    } else{
      state.model = model
    }
  },
  SET_ARCTYPE: (state, arctype) =>{
    if (arctype === undefined){
      //  数据深度拷贝 保证数据初始化成功
      state.arctype = _.cloneDeep(init_arctype)
    } else{
      state.arctype = arctype
    }
  }
}

const actions = {
  // 获取分类模型option列表
  fetchModelOptions({commit}){
    // commit('SET_Model_OPTIONS', [])
    request.get('/api/arctype/model/option').then( resp =>{
      if (resp){
        commit('SET_Model_OPTIONS', resp.data)
      }
    })
  },

  // 获取分类option列表
  fetchTypeOptions({commit, state}, unshift=true){
    // commit('SET_TYPE_OPTIONS', [])
    request.get('/api/arctype/option').then( resp =>{
      if (resp){
        const data = { options: resp.data, un:unshift }
        commit('SET_TYPE_OPTIONS', data)
      }
    })
  },

  // edit page => init data
  // async/awit 异步等待
  async initial({state, dispatch, commit}, id){
    if (id) {
      // edit
      await dispatch('fetchArctype',id)
    } else{
      // post
      commit('SET_ARCTYPE')

    }
    dispatch('fetchModelOptions')
    dispatch('fetchTypeOptions')
  },

  // edit page => get arctype by id
  async fetchArctype({commit, state}, id){
    await request.get(state.arctypeApi+'/'+ id).then( resp =>{
      if (resp) {
        commit('SET_ARCTYPE', resp.data)
      }
    })
  },

  // create/update  arctype => put post
  editArctype({state}){
    const data = state.arctype
    let formData = new FormData();
    Object.keys(data).forEach((key) =>{
      formData.append(key,data[key])
    })
    let config = {
      headers:{
        'Content-Type':'multipart/form-data'
      }
    }
    const id = state.arctype.id
    if (id){
      // put
      return request.put(state.arctypeApi, formData)

    }else {
      // post
      return request.post(state.arctypeApi, formData)
    }
  },

  fetchModel({commit, state}, id){
    if (id === undefined){
      commit('SET_MODEL')
      return
    }
    request.get(state.modelApi+'/'+ id).then( resp =>{
      if (resp) {
        commit('SET_MODEL', resp.data)
      }
    })
  },

  editModel({state}){
    const id = state.model.id
    if (id){
      // put
      return request.put(state.modelApi+'/'+id, state.model)

    } else{
      // post
     return request.post(state.modelApi, state.model)
    }
  }
}


export default {
  namespaced:true,
  state,
  actions,
  mutations
}



