import Vue from 'vue'
import VueI18n from 'vue-i18n'
import enLocale from './en'
import zhLocale from './zh'

Vue.use(VueI18n)

const messages = {
  en:{ ...enLocale },
  zh:{ ...zhLocale }
}
export default new VueI18n({
  locale: localStorage.getItem('i18n') || 'zh',
  messages
})



