import _ from 'lodash'
import request from '@/utils/request'

const init_comment = {
  username: '',
  email: '',
  comment_content:'',
  message_content:'',
  reply_content: '',
  reply_id: 0,
  status: 1
}


const state = {
  comment: init_comment,
  commentAPI: '/api/comment'
}

const mutations = {
  SET_COMMENT: (state, comment) =>{
    if (comment === undefined){
      //  数据深度拷贝 保证数据初始化成功
      state.comment = _.cloneDeep(init_comment)
    } else{
      state.comment = comment
    }
  }
}
const actions = {
  refState({state}, comment){
    request.put('/api/comment/refstate', comment)
  },

  fetchData({state, commit}, uuid){
    request.get(state.commentAPI+'/'+uuid).then(resp =>{
      if (resp){
        commit('SET_COMMENT', resp.data)
      }
    })
  },
  editModel({state}, postData){
    const id = state.comment.id
    console.log(postData)
    return request.post(state.commentAPI, postData)
  }
}

export default {
  namespaced:true,
  state, actions, mutations
}



