import Vue from 'vue'
import App from './App'
/* 多语言支持 */
import i18n from './lang'
/* 路由加载插件 */
import router from './router'
/* 图片加载插件 */
import VueLazyLoad from 'vue-lazyload'
/* 页面加载进度条 */
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import $ from 'jquery'
/* 主题css设定 */
import '../static/global/bootstrap/bootstrap.min.css'
import '../static/global/font-awesome/font-awesome.min.css'
import '../static/system/dist/css/AdminLTE.min.css'
// import '../static/system/dist/css/skins/_all-skins.min.css'
/* checkbox 美化css */
// import '../static/global/iCheck/blue.css'
/* mockjs 数据测试拦截 */
// import './utils/mock'

import VueSweetalert2 from 'vue-sweetalert2'

Vue.use(VueSweetalert2)
import store from './store/index'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.use(ElementUI)

import VueCookies from 'vue-cookies'
Vue.use(VueCookies)

Vue.use(NProgress)

/* 图片加载插件而配置 */
Vue.use(VueLazyLoad,{
  preLoad:1.3,
  error:"/static/system/dist/img/no-image.png",
  // loading:'/static/system/dist/img/loading.gif',
  attempt:1
})

Vue.config.productionTip = false

new Vue({
  router,
  i18n,
  store,
  render:h => h(App)
}).$mount('#app')



