const frontLayout =  () => import('@/views/front/index')
Vue.use(Router)

// front
const front = {
  path:'/',
  name:'',
  component:frontLayout,
  redirect:'/index',
  children:[
    {
      path:'/',
      name:'front_index',
      component: () => import('@/views/front/table/article_list'),
      meta:{ NoAuth: true }
    },
    {
      path:'/category/:cname',
      name:'front_archive_list',
      component: () => import('@/views/front/table/article_list'),
      meta:{ NoAuth: true }
    },
    {
      path:'/detail/:id',
      name:'detail_page',
      component: () => import('@/views/front/table/article'),
      meta:{ NoAuth: true }
    }
  ]
}

// 后台主页路由
