
import request from "@/utils/request"

const state = {
  url:'',
  pageSize:10,
  start: 0,
  list: [],
  total: 0,
  checkbox: []
}
const mutations = {
  SET_CHECKBOX: (state, checkbox) =>{
    state.checkbox = checkbox
  },
  SET_URL: (state, url) =>{
    state.url = url
  },
  SET_PAGESIZE: (state, pageSize) =>{
    state.pageSize = pageSize
  },
  SET_LIST: (state, list) =>{
    state.list = list
  },
  SET_START: (state, start) =>{
    state.start = start
  },
  SET_TOTAL: (state, total) =>{
    state.total = total
  }
}
const actions = {
  fetchList({commit, state}){
    const params = {
      start: state.start,
      length: state.pageSize
    }
    request.get(state.url, {params}).then( resp =>{
      if (resp){
        commit('SET_LIST', resp.data.list)
        commit('SET_TOTAL',resp.data.total)
        commit('SET_CHECKBOX', [])
      }
    })
  },
  remove({dispatch, commit, state}, id){
    let count = 1
    if (Array.isArray(id)){
      count = id.length
    }
    const len = state.list.length - count
    const data = { id }
    return request.delete(state.url, {data}).then( resp =>{
      if (resp){
        if (len === 0){
          commit('SET_START', 0)
        }
        dispatch('fetchList')
      }
      return resp
    })
  }
}


export default {
  namespaced:true,
  state, actions, mutations
}



